<!-- home @author lpf -->
<template>
  <div class='home'>
    <!-- <div class="label-text">请输入查询内容</div> -->
    <div class="pos-area">
      <span>当前城市:</span>
      <span>{{cityName}}</span>
      <span class="changeCity" @click="changeCity">[切换城市]</span>
    </div>
    <mt-field  placeholder="请输入用户名" v-model.trim="searchData"></mt-field>
    <mt-button type="primary" size="large" class="search" @click="search(searchData)">查询</mt-button>
    <div>
       <p class="nearFind">最近搜索</p>
      <Near :lookedList="lookedList" @nearFn="search"/>
    </div>
  </div>
</template>

<script>
import Near from '../components/Near'
export default {
  name: 'home',
  props: ['cityName', 'shouldChangeCity'],
  data () {
    return {
      searchData: '',
      lookedList: ['张撒', 'a京东方', 'dfds']
    }
  },

  components: {
    Near
  },

  computed: {},

  mounted () {
    this.$nextTick(() => {
      this.baiduLocation()
    })
  },

  created () {},

  methods: {
    // search () {
    //   const searchCase = this.searchData
    //   const city = this.cityName
    //   this.$router.push({ name: 'list', params: { searchCase, city } });
    //   this.searchData=""
    // },
    search (val) {
      const searchCase = val
      const city = this.cityName
      this.$router.push({ name: 'list', params: {searchCase, city} })
    },
    baiduLocation () {
      this.$parent.shouldChangeCity = true
      let _that = this
      /*eslint-disable*/
        // 百度地图定位
        var map = new BMap.Map("allmap");
        var point = new BMap.Point(116.331398, 39.897445);
        map.centerAndZoom(point, 12);
        var geoc = new BMap.Geocoder();
        var geolocation = new BMap.Geolocation();
        geolocation.getCurrentPosition(function (r) {
            if (this.getStatus() === BMAP_STATUS_SUCCESS) {
                console.log(r.point);
                geoc.getLocation(r.point, function (rs) {
                    var addComp = rs.addressComponents;
                    console.log(addComp.province + ", " + addComp.city + ", " + addComp.district + ", " + addComp.street + ", " + addComp.streetNumber);
                    if (_that.shouldChangeCity) {
                        _that.$parent.cityName = addComp.city.replace('市', '');
                    }
                });
            }
            else {
                console.log('failed' + this.getStatus());
            }
        }, {enableHighAccuracy: true});
        /*eslint-disable*/
    },
    changeCity(){
      this.$router.push({ name: 'city'})
    }
  }
}

</script>
<style  scoped lang="scss">
.home {
  text-align: left;
  .pos-area {
    font-size: 1.6rem;
    color: #999;
    padding: 1.4rem;
  }
  .changeCity{
    color:#666;
    margin-left: 1rem;
    font-size: 1.2rem;
  }
  .label-text {
    font-size: 1.6rem;
    margin: 1.4rem;
    color: #666;
  }
  .search {
    width: 94%;
    margin: 2.6rem auto;
    letter-spacing: 4px;
    background-color: #1aad19;
  }
  .nearFind {
    margin-left: 1rem;
    color: #999;
    margin-bottom: 1rem;
  }
}
</style>



